<template>
  <div class="w-100 bg-white t-min-height donut custom-chart-div text-center">
    <div :id="'DonutDiv_' + moduleCode + '_' + action + index">
      <loader :isVisible="isLoading"></loader>
      <div
        :class="
          DonutWidgetData.length > 0
            ? 'd-inline-block mt-2 reportBlock align-center c3'
            : 'd-inline-block mt-2 align-center c3'
        "
        :controller="moduleCode"
        :id="'div_WidgetChart_' + action + index"
        :data-test="DonutWidgetData.length"
        :style="{
          'width:60%; float:left; text-align:center;':
            DonutWidgetData.length > 0,
          'width:100%;text-align:center;': DonutWidgetData.length == 0,
        }"
      ></div>
    </div>
    <div v-if="DonutWidgetData.length === 0 && isLoading === false" class="text-danger no-record">
      {{ $t("NoRecordfound") }}
    </div>
  </div>
</template>
<script>
import "d3";
import "c3";
import "c3/c3.min.css";
import DataService from "../../../services/DataService";
export default {
  props: {
    reportName: null,
    chartType: null,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    index: null,
    width: null,
    height: null,
    reportId: null,
  },
  data() {
    return {
      isLoading: true,
      action: "Lead_By_Product",
      charT_TYPE: "donut",
      DonutWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      ChartName: null,
    };
  },
  watch: {
    width: function () {
      this.bindWidget(this);
    },
    height: function () {
      this.bindWidget(this);
    },
    toDate: function () {
      this.FetchData();
    },
    fromDate: function () {
      this.FetchData();
    },
  },
  created: function () {
    var vueObj = this;
    vueObj.FetchData();
  },
  methods: {
    async FetchData() {
      var vueObj = this;
      vueObj.isLoading = true;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      // var url = `moduleName=${vueObj.moduleName}&reportName=Lead_By_Product&fromDate=2023-01-01&toDate=2023-12-31&usersDash=`;
      var url = `moduleName=${vueObj.moduleCode}&reportName=${vueObj.reportName}&&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=`;
      await DataService.GetDashboardData(url).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            if (response.data.result != null) {
              vueObj.DonutWidgetData = response.data.result.data;
              vueObj.bindWidget(vueObj);
            }
          }
        }
        vueObj.isLoading = false;
      });
    },
    // ResizeReportWidget: function (zoom, reportName) {
    //  var name =  '#div_WidgetChart_' + this.action+this.index;
    //     var chart = $(name).data('c3-chart');
    //     chart.revert();
    //     if (zoom == "1") {
    //         chart.resize({ height: 200, width: 440 })
    //     }
    //     else {
    //         if ($(window).width() <= 1280 && $(window).height() <= 1024) {
    //             chart.resize({ height: 350, width: 400 })
    //         }
    //         else {
    //             chart.resize({ height: 500, width: 700 })
    //         }
    //     }
    // },
    bindWidget: function (vueObj) {
      if (vueObj.charT_TYPE && vueObj.DonutWidgetData.length > 0) {
        var chartType = this.charT_TYPE;
        var arrAUP = [];
        // eslint-disable-next-line no-undef
        $.each(vueObj.DonutWidgetData, function (index, element) {
          if (vueObj.action == "LoggedHoursByProject") {
            arrAUP.push([
              element.NAME + " (" + element.VALUE + " h)",
              element.VALUE,
            ]);
          } else {
            arrAUP.push([
              element.NAME + " (" + element.VALUE + ")",
              element.VALUE,
            ]);
          }
        });
        var xCategory = "";
        var yCategory = "";
        // let box = document.querySelector(
        //   "#div_WidgetChart_" + vueObj.action + vueObj.index
        // );
        // let width = box.offsetParent.offsetWidth - 80;
        // let height = box.offsetParent.offsetHeight - 80;
        // var chartWidth = this.width; //200;
        // var chartHeight = this.height; // 200;

        // if (chartType == "donut") {
        //     chartWidth = 440;
        //     chartHeight = 200;
        // }
        // else if (chartType == "pie") {
        //     chartWidth = 480;
        //     chartHeight = 200;
        // }
        //if ($(window).width() <= 1024) {
        //    chartHeight = 120;
        //    chartWidth = 120;
        //}
        if (vueObj.action == "ResponseOverDue") {
          xCategory = vueObj.$t("User");
          yCategory = vueObj.$t("Tickets");
        } else if (vueObj.action == "EventTicketsSoldReport") {
          xCategory = vueObj.$t("Date");
          yCategory = vueObj.$t("Tickets");
        } else if (vueObj.action == "EventBudgetReport") {
          xCategory = vueObj.$t("Category");
          yCategory = vueObj.$t("Budget");
        } else if (vueObj.action == "EventExpensesReport") {
          xCategory = vueObj.$t("Category");
          yCategory = vueObj.$t("Expense");
        }
        var selector = "#div_WidgetChart_" + vueObj.action + vueObj.index;
        // eslint-disable-next-line no-undef
        $(selector).html("");
        // eslint-disable-next-line no-undef
        var Donutchrt = c3.generate({
          bindto: selector,
          data: {
            columns: arrAUP,
            type: chartType,
            empty: {
              label: {
                position: "right",
                text: vueObj.$t("NoRecordfound"),
              },
            },
          },
          tooltip: {
            grouped: false
          },
          pie: {
            label: {
              format: function (value) {
                // eslint-disable-next-line no-undef
                return d3.format("")(value);
              },
            },
          },
          // size: {
          //   width: chartWidth,
          //   height: chartHeight,
          // },
          legend: {
            show: true,
            position: "bottom",
          },
          color: {
            pattern: [
              "#23A4FF",
              "#FF6B6B",
              "#5FB49C",
              "#776274",
              "#916953",
              "#28587B",
              "#F4B701",
              "#272635",
              "#AEE5D8",
              "#FFC482",
              "#3C6E71",
              "#9FA0FF",
              "#D1B1CB",
              "#95B2B8",
              "#EF8354",
              "#72D9C8",
              "#C6EBBE",
              "#56203D",
              "#A09CB0",
              "#5C573E",
            ],
          },
          axis: {
            rotated: false,
            x: {
              label: {
                text: "",
                position: "outer-center",
              },
              categories: [xCategory],
              type: "category",
            },
            y: {
              label: {
                text: yCategory,
                position: "outer-center",
              },
            },
          },
        });
        // eslint-disable-next-line no-undef
        $(selector).data("c3-chart", Donutchrt);
      }
    },
  },
};
</script>
<style src="c3/c3.css"></style>
